<template>
  <div class="ship">
    <div class="ship-button">
      <div class="sku_control singleship">
        <el-button
          type="primary"
          size="small"
          @click="sku_control"
          style="width:120px"
          >海外仓sku对照</el-button
        >
      </div>
      <div class="singleship">
        <el-button
          type="primary"
          size="small"
          @click="canada_ship"
          style="width:120px"
          >加拿大发货</el-button
        >
      </div>
      <div class="singleship">
        <el-button
          type="primary"
          size="small"
          @click="go_long_address"
          style="width:120px"
          >地址超长</el-button
        >
      </div>
      <div class="singleship">
        <el-button
          type="primary"
          size="small"
          @click="false_order"
          style="width:120px"
          >假回传单号查看</el-button
        >
      </div>
      <div class="singleship">
        <el-button
          type="primary"
          size="small"
          @click="shunfengOrder"
          style="width:120px"
          >顺丰订单号</el-button
        >
      </div>
      <div class="singleship">
        <el-button
          type="primary"
          size="small"
          @click="singleShip"
          style="width:120px"
          >单个订单发货</el-button
        >
      </div>
      <div class="singleship downloadNoReturnOrder">
        <el-button type="primary" size="small" @click="exportNoReturn"
          >未回传订单导出</el-button
        >
        <!-- <a
          download=""
          :href="this._url2 + 'server/order_management/no_deliver_order_data'"
          >未回传订单导出</a
        > -->
      </div>
      <div class="firstUpload">
        <span style="font-size:14px">易仓批量发货&nbsp;</span>
        <input
          type="file"
          value="文件上传"
          ref="input1"
          @change="changeFile1"
          accept=".xls, .xlsx"
        />
        <el-button
          style="margin-left: 10px"
          size="mini"
          type="primary"
          @click="submitUpload1"
          >提交</el-button
        >
      </div>
      <div class="secondUpload">
        <span style="font-size:14px">易仓TXT发货&nbsp;</span>
        <input
          type="file"
          value="文件上传"
          ref="input2"
          @change="changeFile2"
          accept=".txt"
        />
        <el-button
          style="margin-left: 10px"
          size="mini"
          type="primary"
          @click="submitUpload2"
          >提交</el-button
        >
      </div>
    </div>

    <!-- 表格相关 -->
    <div class="table_con">
      <el-table
        max-height="750"
        :data="tableData"
        style="width: 100%"
        border
        v-loading="loading"
        element-loading-text="拼命加载中,请稍等"
        size="mini"
      >
        <el-table-column prop="order_id" label="订单号"></el-table-column>
        <el-table-column
          prop="country_code"
          label="国家"
          :width="columnWidth"
        ></el-table-column>
        <el-table-column
          prop="station"
          label="站点"
          :width="columnWidth"
        ></el-table-column>
        <el-table-column prop="dates" label="时间"></el-table-column>
        <el-table-column prop="reason" label="失败原因">
          <template slot-scope="{ row }">
            <span class="reason" @click="showReason(row.reason)">{{
              row.reason
            }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" fixed="right">
          <template slot-scope="scope">
            <div class="operation">
              <el-button
                type="primary"
                @click="reShip(scope.row.order_id)"
                size="mini"
                >重新发货</el-button
              >
              <el-button
                type="danger"
                @click="delShip(scope.row.order_id)"
                size="mini"
                >删除</el-button
              >
              <el-button
                type="warning"
                @click="fix_data(scope.row.order_id)"
                size="mini"
                >修复</el-button
              >
            </div>
          </template>
        </el-table-column>
        <el-table-column label="备注" width="200">
          <template slot-scope="scope">
            <el-input
              type="textarea"
              v-model="scope.row.remark"
              @change="submitRemark(scope.row)"
            ></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 重新发货 -->
    <div class="dialog">
      <el-dialog
        title="重新发货"
        :visible.sync="dialogVisible1"
        :width="dialogWidth"
        @close="addressTips = ''"
      >
        <el-form
          :model="ship_one_form1"
          :rules="rules"
          ref="ruleForm"
          label-width="200px"
        >
          <el-form-item prop="order_id" label="请输入订单编号" required>
            <el-input
              v-model="ship_one_form1.order_id"
              placeholder="请输入订单编号"
            ></el-input>
          </el-form-item>
          <el-form-item label="请选择收件人国家" prop="country_code">
            <el-input v-model="ship_one_form1.country_code"></el-input>
          </el-form-item>

          <!-- <el-form-item label="请填写州/省">
            <el-input
              v-model="ship_one_form.province"
              placeholder="请填写州/省"
            ></el-input>
          </el-form-item>-->
          <el-form-item label="请填写城市" prop="city">
            <el-input
              v-model="ship_one_form1.city"
              placeholder="请填写城市"
            ></el-input>
          </el-form-item>

          <el-form-item prop="address" label="请输入详细地址1" required>
            <el-input
              @change="changeAddress"
              type="textarea"
              :autosize="{ minRows: 4, maxRows: 4 }"
              v-model="ship_one_form1.address"
              placeholder="请输入详细地址1"
            ></el-input>
            <p class="address_Tips">{{ addressTips }}</p>
          </el-form-item>
          <!-- 地址2 -->
          <el-form-item prop="address2" label="请输入详细地址2" required>
            <el-input
              @change="changeAddress2"
              type="textarea"
              :autosize="{ minRows: 4, maxRows: 4 }"
              v-model="ship_one_form1.address2"
              placeholder="请输入详细地址2"
            ></el-input>
            <p class="address_Tips">{{ addressTips2 }}</p>
          </el-form-item>
          <template v-if="ship_one_form1.true_site">
            <el-form-item prop="true_site" label="全部地址">
              <el-input
                v-model="ship_one_form1.true_site"
                :readonly="true"
              ></el-input>
            </el-form-item>
          </template>
          <el-form-item prop="doorplate" label="门牌号" required>
            <el-input v-model="ship_one_form1.doorplate"></el-input>
          </el-form-item>
          <el-form-item prop="name" label="请输入收货人姓名" required>
            <el-input
              v-model="ship_one_form1.name"
              placeholder="请输入收货人姓名"
            ></el-input>
          </el-form-item>

          <el-form-item label="请输入电话" required prop="tel">
            <el-input
              v-model="ship_one_form1.tel"
              placeholder="请输入电话"
            ></el-input>
          </el-form-item>
          <el-form-item label="请输入邮编" required prop="zip_code">
            <el-input
              v-model="ship_one_form1.zip_code"
              placeholder="请输入邮编"
            ></el-input>
          </el-form-item>

          <!-- <el-form-item
            label="自定义编码"
            required
            prop="custom_code"
            style="margin-left:4%"
          >
            <el-input
              v-model="ship_one_form.custom_code"
              placeholder="自定义编码"
            ></el-input>
          </el-form-item>-->
          <div style="width: 50%"></div>

          <el-form-item>
            <el-button
              class="submitbtn"
              type="primary"
              @click="onSubmit1('ruleForm')"
              >提交</el-button
            >
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

    <!-- 单个发货 -->
    <div class="dialog">
      <el-dialog
        title="单个发货"
        :visible.sync="dialogVisible"
        :width="dialogWidth"
      >
        <el-form
          :model="ship_one_form"
          :rules="rules"
          ref="ruleForm"
          label-width="200px"
        >
          <el-form-item prop="number" label="请输入订单编号" required>
            <el-input
              v-model="ship_one_form.number"
              placeholder="请输入订单编号"
            ></el-input>
          </el-form-item>
          <el-form-item label="请选择收件人国家">
            <el-input v-model="ship_one_form.receive_country"></el-input>
          </el-form-item>

          <!-- <el-form-item label="请填写州/省">
            <el-input
              v-model="ship_one_form1.province"
              placeholder="请填写州/省"
            ></el-input>
          </el-form-item>-->
          <el-form-item label="请填写城市">
            <el-input
              v-model="ship_one_form.city"
              placeholder="请填写城市"
            ></el-input>
          </el-form-item>

          <el-form-item prop="address" label="请输入详细地址" required>
            <el-input
              type="textarea"
              :autosize="{ minRows: 4, maxRows: 4 }"
              v-model="ship_one_form.address"
              placeholder="请输入详细地址"
              style="resize: none"
            ></el-input>
          </el-form-item>
          <el-form-item prop="receive_name" label="请输入收货人姓名" required>
            <el-input
              v-model="ship_one_form.receive_name"
              placeholder="请输入收货人姓名"
            ></el-input>
          </el-form-item>

          <el-form-item label="请输入电话" required prop="tel">
            <el-input
              v-model="ship_one_form.tel"
              placeholder="请输入电话"
            ></el-input>
          </el-form-item>
          <el-form-item label="请输入邮编" required prop="postcode">
            <el-input
              v-model="ship_one_form.postcode"
              placeholder="请输入邮编"
            ></el-input>
          </el-form-item>

          <!-- <el-form-item
            label="自定义编码"
            required
            prop="custom_code"
            style="margin-left: 4%"
          >
            <el-input
              v-model="ship_one_form1.custom_code"
              placeholder="自定义编码"
            ></el-input>
          </el-form-item>-->
          <div style="width: 50%"></div>

          <el-form-item>
            <el-button
              class="submitbtn"
              type="primary"
              @click="onSubmit('ruleForm')"
              >提交</el-button
            >
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

    <!-- 修复弹框 -->
    <div class="dialog">
      <el-dialog
        title="修复"
        :visible.sync="dialogVisible2"
        :width="dialogWidth"
      >
        <el-form
          :model="ship_one_form2"
          :rules="rules1"
          ref="ruleForm"
          label-width="200px"
        >
          <el-form-item prop="number" label="请输入海外仓订单编号" required>
            <el-input
              v-model="ship_one_form2.number"
              placeholder="请输入海外仓订单编号"
            ></el-input>
          </el-form-item>

          <div style="width: 50%"></div>
          <el-form-item>
            <el-button
              class="submitbtn"
              type="primary"
              @click="onSubmit2('ruleForm')"
              >提交</el-button
            >
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

    <!-- 失败原因详情 -->
    <div class="dialog">
      <el-dialog
        title="失败原因详情"
        :visible.sync="reasonVisible"
        :width="dialogWidth"
        @close="reasonClose"
        center
      >
        <div>{{ reason }}</div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="reasonVisible = false">关 闭</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      columnWidth: "",
      // -----------------------原有的--------------------------
      // 新增
      submitFlag1: false,
      submitFlag2: false,
      addressTips: "",
      addressTips2: "",
      tableData: [
        {
          order_id: "",
          country_code: "",
          station: "",
          reason: ""
        }
      ],
      dialogVisible: false,
      dialogVisible1: false,
      receive_countries: ["美国", "日本"],
      //单个订单发货数据
      ship_one_form: {
        order_id: "",
        country_code: "",
        // province: "",
        city: "",
        address: "",
        name: "",
        tel: "",
        zip_code: ""
        // custom_code: "",
      },
      rules: {
        number: [
          {
            required: true,
            message: "请输入订单编号",
            trigger: "blur"
          }
        ],
        address: [
          {
            required: true,
            message: "请输入详细地址",
            trigger: "blur"
          }
        ],
        receive_name: [
          {
            required: true,
            message: "请输入收货人姓名",
            trigger: "blur"
          }
        ],
        tel: [{ required: true, message: "请输入电话", trigger: "blur" }],
        postcode: [{ required: true, message: "请输入邮编", trigger: "blur" }],
        // custom_code: [
        //   { required: true, message: "自定义编码", trigger: "blur" },
        // ],
        doorplate: [
          { required: true, message: "请输入门牌号", trigger: "blur" }
        ]
      },
      //重新发货数据
      ship_one_form1: {
        order_id: "",
        country_code: "",
        // province: "",
        city: "",
        address: "",
        address2: "",
        name: "",
        tel: "",
        zip_code: "",
        doorplate: "", //门牌号
        true_site: "" // 全部地址
        // custom_code: "",
      },
      //修复弹框
      dialogVisible2: false,
      //修复数据
      ship_one_form2: {
        order_id: "",
        number: ""
      },
      rules1: {
        number: [
          {
            required: true,
            message: "请输入海外仓订单编号",
            trigger: "blur"
          }
        ]
      },
      repair_order_id: "",
      formData1: null,
      formData2: null,
      loading: false,
      // 单个发货对话框宽度
      dialogWidth: "0",
      files: "",

      // 查看原因详情对话框
      reasonVisible: false,
      reason: ""
    };
  },
  props: ["type"],
  computed: {
    // 获取屏幕宽度
    clientWidth() {
      return this.$store.state.clientWidth;
    }
  },
  watch: {
    // 监听屏幕宽度
    clientWidth(val) {
      console.log("监听", val);
      if (val < 480 || val == 480) {
        this.columnWidth = "40";
      } else {
        this.columnWidth = "";
      }
    }
  },
  mounted() {
    this.get_data();
    this.initDialogWidth();
    this.setColumnWidth();
    window.onresize = this.$utils.throttle(() => {
      this.setDialogWidth();
      this.setColumnWidth();
    }, 600);
  },
  methods: {
    splitUrl(url) {
      return url.replace("/home/by_operate/", "");
    },
    exportNoReturn() {
      let url = this._url2 + "server/order_management/no_deliver_order_data/";
      this.$axios.get(url).then(res => {
        console.log(res);
        let url = res.data.data;
        if (url) {
          let resultUrl = this.splitUrl(url);
          console.log(resultUrl);
          let a = document.createElement("a");
          a.setAttribute("href", this._url2 + resultUrl);
          document.body.appendChild(a);
          a.click();
        } else {
          this.$message.warning("暂无数据可供下载");
        }
      });
    },
    /**
     * @description: 设置表格某些项宽度
     * @param {*}
     * @return {*}
     */
    setColumnWidth() {
      let val = document.body.clientWidth;
      if (val < 480 || val == 480) {
        this.columnWidth = "40";
      } else {
        this.columnWidth = "";
      }
    },
    submitRemark(rowData) {
      console.log(rowData);
      let url = this._url2 + "server/order_management/err_order_remark/";
      this.$axios
        .get(url, {
          params: {
            remark: rowData.remark,
            order_id: rowData.order_id
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message.success("请求成功");
          } else {
            this.message.warning("请求未成功");
          }
        });
    },
    sku_control() {
      this.$router.push("/SkuControl");
    },
    /**
     * @description: 根据屏幕变化改变dialog宽度
     * @param {*}
     * @return {*}
     */
    setDialogWidth() {
      console.log(document.body.clientWidth);
      let val = document.body.clientWidth;
      const def = 800; // 默认宽度
      if (val < def) {
        this.dialogWidth = "100%";
      } else {
        this.dialogWidth = def + "px";
      }
    },
    /**
     * @description: 页面刷新根据屏幕大小给dialog宽度赋值
     * @param {*}
     * @return {*}
     */
    initDialogWidth() {
      let val = document.body.clientWidth;
      if (val < 992) {
        this.dialogWidth = "100%";
      } else {
        this.dialogWidth = "60%";
      }
    },
    // 单个订单发货
    singleShip() {
      this.dialogVisible = true;
    },
    handleClose1(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    //单个订单发货提交
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("验证通过，发起请求！");
          this.dialogVisible = false;
          let url = "server/order_management/single_order_deliver/";
          let formData = new FormData();
          formData.append("order_id", this.ship_one_form.order_id);
          formData.append("country_code", this.ship_one_form.country_code);
          formData.append("city", this.ship_one_form.city);
          formData.append("address", this.ship_one_form.address);
          formData.append("name", this.ship_one_form.name);
          formData.append("tel", this.ship_one_form.tel);
          formData.append("zip_code", this.ship_one_form.zip_code);
          console.log(this.ship_one_form);
          this.$axios.post(this._url2 + url, formData).then(res => {
            console.log("编辑数据提交", res.data);
            if (res.data.code === 200) {
              alert(res.data.msg);
              window.location.reload();
            } else {
              alert(res.data.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //重新发货提交
    onSubmit1(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("重新发货！");
          console.log("测试地址2");
          this.addressTips = "";
          this.dialogVisible1 = false;
          let url = "server/order_management/single_order_deliver/";
          let formData = new FormData();
          formData.append("order_id", this.ship_one_form1.order_id);
          formData.append("country_code", this.ship_one_form1.country_code);
          formData.append("city", this.ship_one_form1.city);
          formData.append("address", this.ship_one_form1.address);
          formData.append("address2", this.ship_one_form1.address2);
          formData.append("name", this.ship_one_form1.name);
          formData.append("tel", this.ship_one_form1.tel);
          formData.append("zip_code", this.ship_one_form1.zip_code);
          formData.append("err_order", "Y");
          formData.append("doorplate", this.ship_one_form1.doorplate);
          console.log(formData);
          this.$axios.post(this._url2 + url, formData).then(res => {
            console.log("编辑数据提交", res.data);
            if (res.data.code === 200) {
              alert(res.data.msg);
              window.location.reload();
            } else {
              alert(res.data.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    changeFile1() {
      console.log(this.type === "template");
      console.log("提交", this.submitFlag1);
      if (this.type === "template") return;
      console.log(1);
      let files = this.$refs.input1.files[0];
      console.log(files.name);
      if (files.name) {
        let hz = files.name.split(".")[1];
        console.log(hz);
        if (hz !== "xls" && hz !== "xlsx") {
          this.$refs.input1.files = null;
          alert("请上传表格文件");
          return;
        }
      }
      // 新增
      this.submitFlag1 = true;
      this.formData1 = new FormData();
      this.formData1.append("files", files);
    },
    changeFile2() {
      console.log(this.type === "template");
      console.log("提交", this.submitFlag2);
      if (this.type === "template") return;
      console.log(2);
      let files = this.$refs.input2.files[0];
      if (files.name) {
        let hz = files.name.split(".")[1];
        if (hz !== "txt") {
          this.$refs.input2.files = null;
          alert("请上传txt文件");
          return;
        }
      }
      // 新增
      this.submitFlag2 = true;
      this.formData2 = new FormData();
      this.formData2.append("files", files);

      console.log("提交", this.submitFlag2);
    },
    //易仓批量发货
    submitUpload1() {
      console.log("提交", this.submitFlag1);
      // 新增
      if (this.type === "template" || !this.submitFlag1) return;
      // if (this.type === "template") return;
      console.log(3);
      let url = "server/order_management/yc_batch_create_order/";
      this.loading = true;
      this.submitFlag1 = false;
      this.$axios.post(this._url2 + url, this.formData1).then(res => {
        if (res.data.code === 200) {
          alert(res.data.msg);
          window.location.reload();
        } else {
          alert(res.data.msg);
          window.location.reload();
        }
      });
    },
    // 顺丰订单号
    shunfengOrder() {
      this.$router.push("/shunfengOrder");
    },
    //易仓txt发货
    submitUpload2() {
      console.log("标签", this.submitFlag2);
      // 新增
      if (this.type === "template" || !this.submitFlag2) return;

      // if (this.type === "template") return;
      console.log(4);
      let url = "server/order_management/txt_order/";
      this.loading = true;
      this.submitFlag2 = false;
      this.$axios.post(this._url2 + url, this.formData2).then(res => {
        if (res.data.code === 200) {
          alert(res.data.msg);
          window.location.reload();
        } else {
          alert(res.data.msg);
          window.location.reload();
        }
      });
    },
    // 重新发货
    reShip(row) {
      this.dialogVisible1 = true;
      console.log(row);
      let url = "server/order_management/err_order_detail/";
      let that = this;
      this.$axios
        .get(this._url2 + url, { params: { order_id: row } })
        .then(res => {
          console.log(res.data);
          if (res.data.code === 200) {
            that.ship_one_form1 = res.data.data[0] || {};
          } else {
            alert(res.data.msg);
          }
        });
    },
    // 重新发货 地址长度提示

    changeAddress(val) {
      console.log(val);
      this.addressTips = "已输入" + val.length + "个字符";
    },

    changeAddress2(val) {
      console.log(val);
      this.addressTips2 = "已输入" + val.length + "个字符";
    },

    // 删除订单
    delShip(row) {
      if (confirm("确定删除")) {
        console.log(row);
        let url = "server/order_management/err_order_delete/";
        let that = this;
        let formData = new FormData();
        formData.append("order_id", row);
        this.$axios.post(this._url2 + url, formData).then(res => {
          console.log(res.data);
          if (res.data.code === 200) {
            alert("删除成功");
            window.location.reload();
          } else {
            alert(res.data.msg);
          }
        });
      }
    },
    //获取表格数据
    get_data() {
      let url = "server/order_management/err_order_select/";
      let that = this;
      this.loading = true;
      this.$axios.post(this._url2 + url).then(res => {
        console.log(res.data);
        if (res.data.code === 200) {
          that.tableData = res.data.data;
          that.loading = false;
        } else {
          alert(res.data.msg);
        }
      });
    },
    //打开修复弹框
    fix_data(row) {
      this.dialogVisible2 = true;
      this.repair_order_id = row;
    },
    //修复提交
    onSubmit2(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.dialogVisible2 = false;
          let url = "server/order_management/repair_defeated_order/";
          let formData = new FormData();
          formData.append("order_id", this.repair_order_id);
          formData.append("order_code", this.ship_one_form2.number);
          this.$axios.post(this._url2 + url, formData).then(res => {
            if (res.data.code === 200) {
              alert(res.data.msg);
              window.location.reload();
            } else {
              alert(res.data.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //跳转地址超长
    go_long_address() {
      this.$router.push({
        path: "/Address_long"
      });
    },
    //假回传单号查看
    false_order() {
      this.$router.push({
        path: "/FalseOrder"
      });
    },
    // 加拿大发货

    canada_ship() {
      this.$router.push("/CanadaShip");
    },
    /**
     * @description: 失败详情对话框
     * @param {*} reason
     * @return {*}
     */
    showReason(reason) {
      this.reason = reason;
      this.reasonVisible = true;
    },
    /**
     * @description: 对话框关闭
     * @param {*}
     * @return {*}
     */
    reasonClose() {
      this.reason = "";
    }
  }
};
</script>
<style scoped>
.singleship.downloadNoReturnOrder {
}
.downloadNoReturnOrder a {
  font-size: 12px;
  color: #fff;
  padding: 7px 5px;
  background: #409eff;

  display: inline-block;
  border-radius: 4px;
}
.ship .el-form .address_Tips {
  position: absolute;
  left: -114px;
  top: 20px;
  width: 200px;
  color: rgb(201, 21, 33);
  font-size: 14px;
}
.ship .el-form .submitbtn {
  width: 100%;
  margin-left: 0 !important;
}
.ship .el-form {
  border: 1px solid #e5e5e5;
  padding: 40px 50px 20px 0px;
  border-radius: 6px;
  margin-bottom: 20px;
}
.ship .easywarehouse.el-form {
  text-align: center;
  border: 1px solid #e5e5e5;
  border-radius: 6px;
  padding-top: 20px;
}
.ship .easywarehouse.el-form > .el-form-item {
  width: 350px;
  margin: 0px auto;
}
.ship .el-form-item__content {
  text-align: center;
  display: flex;
  align-items: center;
  margin-top: 3px;
}
.ship .el-form-item__content > .el-button.el-button--primary {
  margin-left: -200px;
}
.card_com .ship .el-select {
  width: 100%;
}
</style>
<style scoped>
.ship {
  width: 100vw;
}
.firstUpload .input[type="file"] {
  width: 44%;
}
.shipOption {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}
.singleship,
.firstUpload,
.secondUpload {
  background: rgba(255, 255, 255, 0.9);
  padding: 6px;
  height: 44px;
  margin: 12px;
  font-size: 0.8rem;
}
/* 修改 */
.singleship {
  margin: 12px 8px;
}
.firstUpload,
.secondUpload {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.singleship >>> .el-button {
  display: block;
  margin: 0 auto;
}
.fbm_manual .table_con >>> .cell {
  padding: 5px 10px !important;
}
/* 按钮相关 */
.ship-button {
  display: flex;
  flex-wrap: wrap;
}
.reason {
  white-space: pre-wrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  cursor: pointer;
  color: #f56c6c;
}
/* 屏幕768以下布局 */
@media screen and (max-width: 768px) {
  .singleship {
    width: 45%;
  }
  .ship-button {
    justify-content: center;
  }
  .firstUpload,
  .secondUpload {
    width: 100%;
    justify-content: center;
  }
  .operation {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .operation .el-button {
    width: 100%;
    text-align: center;
    padding: 6px 0 6px;
  }
  .operation .el-button--danger,
  .operation .el-button--warning {
    margin-left: 0;
  }
}
@media screen and (max-width: 480px) {
  .singleship {
    width: 40%;
  }
  .firstUpload,
  .secondUpload {
    width: 100%;
    justify-content: center;
  }
  .firstUpload span,
  .secondUpload span {
    display: inline-block;
    min-width: 20%;
    text-align: right;
  }
  .firstUpload input[type="file"],
  .secondUpload input[type="file"] {
    max-width: 50%;
  }
}
</style>
<style>
.el-textarea .el-textarea__inner {
  resize: none;
}
/* 屏幕在768以下的*/
@media screen and (max-width: 768px) {
  .ship .el-form {
    padding: 6px !important;
  }
  .ship .el-form-item__content {
    margin-left: 0 !important;
  }
  .ship .el-form-item__label {
    text-align: left !important;
  }
}
</style>



